.eb-gallery-img-wrapper .eb-gallery-img-content {
  position: relative;
  display: block;
}
.eb-gallery-img-wrapper .eb-gallery-img-content .eb-gallery-img-caption {
  position: absolute;
  left: 50%;
  width: auto;
  min-width: 80%;
  transform: translateX(-50%);
  margin-left: 0 !important;
  margin-right: 0 !important;
  font-size: 0.8em;
  line-height: 1em;
}
.eb-gallery-img-wrapper.grid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(3, auto);
  grid-auto-flow: dense;
}
.eb-gallery-img-wrapper.grid .eb-gallery-img-content img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.eb-gallery-img-wrapper.masonry {
  display: block;
  columns: 3;
}
.eb-gallery-img-wrapper.masonry .eb-gallery-img-content {
  break-inside: avoid;
}
.eb-gallery-img-wrapper.caption-on-hover .eb-gallery-img-content .eb-gallery-img-caption {
  opacity: 0;
}
.eb-gallery-img-wrapper.caption-on-hover .eb-gallery-img-content:hover .eb-gallery-img-caption {
  opacity: 1;
  transition: all 0.3s ease-in-out;
}

.eb-lightbox-sortable-item {
  display: flex;
  justify-content: space-between;
  border: 1px solid;
  line-height: 2.5em;
}

.eb-lightbox-sortable-title {
  cursor: pointer;
  flex: 12;
  padding-left: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 200px;
  white-space: nowrap;
}

.eb-gallery-upload-button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 60px;
  width: 100%;
  background: #e3e3e3;
  color: #666666;
}

.eb-lightbox-delete-icon {
  position: absolute;
  margin-left: 88%;
  margin-top: 1%;
  color: #f44336;
}

.eb-lightbox-image-wrapper {
  position: relative;
}

.eb-is-sorting {
  outline: 4px solid;
}

@media (min-width: 600px) {
  .columns-8 a {
    flex-basis: calc(12.5% - 16px);
  }

  .columns-7 a {
    flex-basis: calc(14.28% - 16px);
  }

  .columns-6 a {
    flex-basis: calc(16.667% - 16px);
  }

  .columns-5 a {
    flex-basis: calc(20% - 16px);
  }

  .columns-4 a {
    flex-basis: calc(25% - 16px);
  }

  .columns-3 a {
    flex-basis: calc(33.33% - 16px);
  }

  .columns-2 a {
    flex-basis: calc(50% - 16px);
  }

  .columns-1 a {
    flex-basis: 100%;
  }
}
.eb-gallery-img-link {
  position: relative;
  cursor: pointer;
}

.eb-lightbox-caption-input-wrapper {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  margin-top: 10px;
}

.caption-style-1 {
  overflow: hidden;
}

.caption-style-1 .eb-gallery-img-caption {
  opacity: 0;
}

.caption-style-1 .eb-gallery-img {
  transition: all 0.25s ease-in-out;
  filter: grayscale(100%) !important;
}

.caption-style-1 .eb-gallery-img-content:hover .eb-gallery-img {
  filter: grayscale(0%) !important;
}

.caption-style-1 .eb-gallery-img-content:hover .eb-gallery-img-caption {
  opacity: 1;
  transition: all 0.3s ease-in-out;
}

.caption-style-2 {
  overflow: hidden;
}

.caption-style-2 .eb-gallery-img-content .eb-gallery-img-caption {
  opacity: 0;
  top: 100%;
  width: 100% !important;
  height: 100%;
  background: rgba(0, 0, 0, 0.65);
  margin: 0px !important;
  left: 0 !important;
  transform: translateX(0%);
}

.caption-style-2 .eb-gallery-img {
  transition: all 0.25s ease-in-out;
}

.caption-style-2 .eb-gallery-img-content:hover .eb-gallery-img {
  filter: drop-shadow(2px 4px 4px gray) !important;
}

.caption-style-2 .eb-gallery-img-content:hover .eb-gallery-img-caption {
  opacity: 0.7 !important;
  transform: translateY(-100%);
  transition: all 0.25s ease-in-out;
  width: 100%;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

