.eb-lightbox-input-label-wrapper {
  display: flex;
  justify-content: space-around;
  margin-bottom: 10px;
}

.eb-lightbox-input-label {
  align-self: center;
}

.eb-lightbox-btn-wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.eb-lightbox-save-btn {
  margin-right: 10px;
}